___CSS_LOADER_EXPORT___.push([module.id, `body {
	box-sizing: border-box;
  margin: 0;
  line-height: normal;
  font-family: var(--font-family-sans);
  font-weight: normal;
  color: #222;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: -0.5px;
  

  *,
  *::before,
  *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    word-break: keep-all;     
    overflow-wrap: break-word; 
  }

  .blind { 
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    overflow: hidden;
    clip-path: polygon(0 0, 0 0, 0 0);
  }
  
  .hidden {
    display: none !important;
  }

  ul,
  ol,
  li {
    list-style: none;
  }

  a {
    color: inherit;
    cursor: pointer;
    text-decoration: none;
    &:hover,
    &:focus {
      outline: none;
      text-decoration: none;
    }
  }

  table {
    table-layout: fixed;
    width: 100%;
    border-collapse: collapse;
  }

  button {
    border: 0;
    background-color: transparent;
    color: inherit;
    cursor: pointer;
    &:focus {
      outline: none;
    }
    &:disabled {
      cursor: not-allowed;
    }
  }

  select,
  input,
  textarea,
  button {
    font-family: var(--font-family-sans);
    appearance: none;
  }

  input:placeholder-shown {
    text-overflow: ellipsis;
  }

  input[type="file"] {
    position: absolute;
    width: 0;
    height: 0;
  }

  input[type="number"] {
    -moz-appearance: textfield;
  }

  input[type="search"] {
    &::-webkit-search-cancel-button {
      display: none;
    }
  }

  input[type="date"] {
    appearance: none;
    
    &::placeholder {
      display: none;
    }
    &::-webkit-date-and-time-value {
      text-align: left;
    }
    &::-webkit-calendar-picker-indicator {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background-image: none;
      color: var(--placdeholder);
    }
    &::-webkit-datetime-edit {
      opacity: 1;
    }
  }
  
  select {
    border: 0;
    background-color: transparent;
    cursor: pointer;
    &:focus {
      outline: none;
    }
    &::-ms-expand {
      display: none;
    }
  }

  input,
  textarea {
    font-size: inherit;
    border: 0;
    background-color: #fff;
    &:focus {
      outline: none;
    }
    &::placeholder {
      /* Chrome, Safari, Firefox */
      -webkit-text-fill-color: currentColor;
      color: var(--placeholder);
      opacity: 1;
    }
    &:-ms-input-placeholder {
      /* IE, Edge */
      color: var(--placeholder);
      opacity: 1;
    }
    &:disabled {
      -webkit-text-fill-color: currentColor;
      opacity: 1;
      cursor: not-allowed;
    }
    &[readonly],
    &:disabled {
      &::placeholder {
        /* Chrome, Safari, Firefox */
        -webkit-text-fill-color: currentColor;
        color: rgba(#222, 0.2);
        opacity: 1;
      }
      &:-ms-input-placeholder {
        /* IE, Edge */
        color: rgba(#222, 0.2);
        opacity: 1;
      }
    }
    &::-ms-clear,
    &::-ms-reveal {
      /* IE, Edge */
      display: none;
    }
    &::-webkit-inner-spin-button,
    &::-webkit-outer-spin-button {
      appearance: none;
    }
  }

  textarea {
    resize: none;
  }

  fieldset {
    border: 0;
    min-width: 0;
  }

  strong,
  b {
    font-weight: 700;
  }
  em{
    font-style: normal;
  }
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAAA;CACC,sBAAsB;EACrB,SAAS;EACT,mBAAmB;EACnB,oCAAoC;EACpC,mBAAmB;EACnB,WAAW;EACX,uBAAuB;EACvB,mCAAmC;EACnC,kCAAkC;EAClC,sBAAsB;;;EAGtB;;;IAGE,sBAAsB;IACtB,SAAS;IACT,UAAU;IACV,6CAA6C;IAC7C,oBAAoB;IACpB,yBAAyB;EAC3B;;EAEA;IACE,kBAAkB;IAClB,UAAU;IACV,WAAW;IACX,YAAY;IACZ,gBAAgB;IAChB,iCAAiC;EACnC;;EAEA;IACE,wBAAwB;EAC1B;;EAEA;;;IAGE,gBAAgB;EAClB;;EAEA;IACE,cAAc;IACd,eAAe;IACf,qBAAqB;IACrB;;MAEE,aAAa;MACb,qBAAqB;IACvB;EACF;;EAEA;IACE,mBAAmB;IACnB,WAAW;IACX,yBAAyB;EAC3B;;EAEA;IACE,SAAS;IACT,6BAA6B;IAC7B,cAAc;IACd,eAAe;IACf;MACE,aAAa;IACf;IACA;MACE,mBAAmB;IACrB;EACF;;EAEA;;;;IAIE,oCAAoC;IACpC,gBAAgB;EAClB;;EAEA;IACE,uBAAuB;EACzB;;EAEA;IACE,kBAAkB;IAClB,QAAQ;IACR,SAAS;EACX;;EAEA;IACE,0BAA0B;EAC5B;;EAEA;IACE;MACE,aAAa;IACf;EACF;;EAEA;IACE,gBAAgB;;IAEhB;MACE,aAAa;IACf;IACA;MACE,gBAAgB;IAClB;IACA;MACE,kBAAkB;MAClB,MAAM;MACN,OAAO;MACP,QAAQ;MACR,SAAS;MACT,WAAW;MACX,YAAY;MACZ,sBAAsB;MACtB,0BAA0B;IAC5B;IACA;MACE,UAAU;IACZ;EACF;;EAEA;IACE,SAAS;IACT,6BAA6B;IAC7B,eAAe;IACf;MACE,aAAa;IACf;IACA;MACE,aAAa;IACf;EACF;;EAEA;;IAEE,kBAAkB;IAClB,SAAS;IACT,sBAAsB;IACtB;MACE,aAAa;IACf;IACA;MACE,4BAA4B;MAC5B,qCAAqC;MACrC,yBAAyB;MACzB,UAAU;IACZ;IACA;MACE,aAAa;MACb,yBAAyB;MACzB,UAAU;IACZ;IACA;MACE,qCAAqC;MACrC,UAAU;MACV,mBAAmB;IACrB;IACA;;MAEE;QACE,4BAA4B;QAC5B,qCAAqC;QACrC,sBAAsB;QACtB,UAAU;MACZ;MACA;QACE,aAAa;QACb,sBAAsB;QACtB,UAAU;MACZ;IACF;IACA;;MAEE,aAAa;MACb,aAAa;IACf;IACA;;MAEE,gBAAgB;IAClB;EACF;;EAEA;IACE,YAAY;EACd;;EAEA;IACE,SAAS;IACT,YAAY;EACd;;EAEA;;IAEE,gBAAgB;EAClB;EACA;IACE,kBAAkB;EACpB;AACF","sourcesContent":["body {\r\n\tbox-sizing: border-box;\r\n  margin: 0;\r\n  line-height: normal;\r\n  font-family: var(--font-family-sans);\r\n  font-weight: normal;\r\n  color: #222;\r\n  scroll-behavior: smooth;\r\n  -webkit-font-smoothing: antialiased;\r\n  -moz-osx-font-smoothing: grayscale;\r\n  letter-spacing: -0.5px;\r\n  \r\n\r\n  *,\r\n  *::before,\r\n  *::after {\r\n    box-sizing: border-box